  word-break: break-all;
  box-shadow: 0 2rpx 8rpx rgba(0,0,0,0.05);
}

.msg-image {
  max-width: 450rpx;
  max-height: 400rpx;
  border-radius: 12rpx;
  display: block;
}

.location-msg {
  width: 480rpx;
  border-radius: 8rpx;
  overflow: hidden;
  .map-thumb {
    width: 100%;
    height: 280rpx;
    display: block;
  }
  .map-info {
    padding: 15rpx 20rpx;
    background-color: #fff;
  }
  .map-title {
    font-size: 28rpx;
    font-weight: bold;
    color: #333;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin-bottom: 8rpx;
  }
  .map-address {
    font-size: 24rpx;
    color: #777;
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }
}

.revoked-text {
  font-size: 24rpx;
  color: #999;
}

.input-bar {
  position: fixed;
  left: 0;
  right: 0;
  display: flex;
